<template>
  <el-aside style="width: 250px" class="submenu_aside">
    <el-scrollbar wrap-class="scrollbar_wrapper">
      <el-menu
        class="sidebar_menu_wrapper"
        :default-active="activeIndex"
        mode="vertical"
      >
        <sidebar-item
          v-for="sidebarItem in routes"
          :key="sidebarItem.key"
          :routes="sidebarItem"
        >
        </sidebar-item>
      </el-menu>
      <div>
        <el-button
          type="primary"
          icon="icon-tuichu"
          plain
          @click="logout"
          class="iconfont logoutBtn"
          >{{ $t("btn.logout") }}</el-button
        >
      </div>
    </el-scrollbar>
  </el-aside>
</template>

<script>
import { routes } from "@/router";
import SidebarItem from "./components/SideBarItem";
export default {
  components: {
    SidebarItem,
  },
  computed: {
    activeIndex() {
      return this.$route.path.split("/")[1];
    },
    routes() {
      return routes.filter((item) => "children" in item);
    },
  },
  methods: {
    // 是否只有一层children，如果只有一层，to=path+"/"+children[0].path
    // 如果多层，to = path + children[0].path + children[0].path
    hasOneLayerChildren() {
      routes.map((route) => route.children && route.children[0].children);
    },
    logout() {
      localStorage.removeItem("token");
      localStorage.removeItem("vuex");
      this.$router.push("/");
    },
  },
};
</script>

<style lang="scss" scoped>
.submenu_aside {
  border-right: solid 1px #e6e6e6;
  min-height: 100%;
  max-height: 100vh;
  .el-scrollbar {
    height: 100%;
  }
}
.scrollbar_wrapper {
  height: 100%;
}
.logoutBtn {
  margin-left: 90px;
  margin-top: 3em;
  margin-bottom: 2em;
}
</style>
